import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/retry';
import 'rxjs/add/operator/map';

@Injectable()
export class ApiProvider {

   public linkAIP : string ='anime.phimqq.com/api';

  constructor(public _http: Http) {}

     callApi(url: string, method: string, data: string = null,request: RequestOptions = null): Observable<any>
    {
        
        if (request == null) request = new RequestOptions();
       
        
        if (request.headers == null) request.headers = new Headers();
    
         if (method.toLowerCase() == "post") {
      
            request.headers.append("Content-Type", "application/x-www-form-urlencoded");
      
            return this._http.post(url, data, request).map((response) => response.json()).retry(5);
        }
        
        if (method.toLowerCase() == "get") {
          return this._http.get(url,request).map((response) => response.json()).retry(5);
      }
    }
     get_movie_detail(id)
    {
      if(id==undefined){
        return false;
      }
      return new Promise((resovle,reject)=>{
        this.callApi("https://"+this.linkAIP+"/?cmd=api_detail_movie&id_movie="+id,"GET").subscribe((res)=>{
          resovle(res)
       });
       
      })
        
    }

    get_episode_flim(f_id,id_episode):any{
      return new Promise((resolve,reject)=>{
        this.callApi(`https://${this.linkAIP}/?cmd=api_get_info_movie&id_movie=${f_id}&id_episode=${id_episode}`,'get')
        .subscribe(
          result=>{
            resolve(result)
          },
          err => reject(err)
        )
      })
    }


}


