import { Component } from '@angular/core';
import { NavController, LoadingController, IonicPage, Segment } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

// @IonicPage(
//   {
//     name:'home',
//     segment:'Home',
//   }
// )
export class HomePage {

  public movie_decu;
  public movie_moi;
  public icons:string = 'camera';
  constructor(public navCtrl: NavController
    ,public api:ApiProvider,
    public loadControl:LoadingController,
    ) {

  }
  //
  async  ionViewDidLoad()
  {
    // load danh sach phim
    let load_ds_phim = this.loadControl.create({
      spinner:'crescent'
    })
    load_ds_phim.present();
    // load danh sach phim
    let listRes =  await this.get_movies();
    this.movie_decu = listRes.movie_decu;
    this.movie_moi = listRes.movie_moi;
    
    load_ds_phim.dismiss();
  }

  async get_movies(): Promise<any>{
    return new Promise((resolve, reject) => {
      this.api.callApi(`https://${this.api.linkAIP}/?cmd=api_get_list_movie`, "get")
      .subscribe((result) => {
        resolve(result);
        console.log(result);
      });
    })
  }
  
  push_detail(movie){
    this.navCtrl.push('detail',{id:movie.id,movie:movie})
  }
}



